//Precargador
.cd-loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1001;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fff;
  color: $gris-oscuro;
  &__grid {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation-delay: 0.2s;
  }
  &__item {
    width: 100%;
    height: 100%;
    background: #fff url('../img/footwear.svg') no-repeat center center;
    animation: cd-loader-scale-delay 1.3s infinite ease-in-out;
  }
}

@keyframes cd-loader-scale-delay {
  0%,
  70%,
  100% {
    transform: scale3D(1, 1, 1);
  }
  35% {
    transform: scale3D(0.5, 0.5, 1);
  }
}
